import React, { useState } from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import Modal from '@modules/Modal';

import EditAbout from './EditAbout';
import EditPhoneNumber from './EditPhoneNumber';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const ShoutBioContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const CompanyLogo = styled.div`
    height: 200px;
    width: 200px;
    border-radius: 15px;
    background-color: ${colors.grey};
`;
const Title = styled.div`
    font-size: 18px;
    font-weight: 600;
    margin: 10px;
`;

const Table = styled.div`
    margin-top: 15px;
    box-shadow: 1px 1px 4px 0px rgb(0 0 0 / 25%);
    padding: 10px;
    border-radius: 15px;
`;

const TableContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 10px;
`;
const TableTitle = styled.div`
    width: 100%;
    font-weight: 700;
`;
const TableContent = styled.div`
    flex: 1;
`;

const EditButton = styled.span`
    cursor: pointer;
    color: ${colors.defaultColor};
    font-weight: 600;
    margin-right: 15px;
`;

export default function Profile() {
    const [editAboutModal, setEditAboutModal] = useState(false);
    const [editPhoneNumberModal, setEditPhoneNumberModal] = useState(false);

    return (
        <>
            <div className="container">
                <Container>
                    <CompanyLogo></CompanyLogo>
                    <ShoutBioContainer>
                        <Title>Gavel NG</Title>
                        <div>Nigeria</div>
                        <div>08128833884848</div>
                        <div>hello@gavel.ng</div>
                    </ShoutBioContainer>

                    <Table>
                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>Phone Number(s)</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>
                                    08182828282, 0908838383838, 08138383838338{' '}
                                    <EditButton onClick={() => setEditPhoneNumberModal(true)}>Edit</EditButton>
                                </TableContent>
                            </div>
                        </TableContainer>

                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>Email(s)</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>
                                    hello@gavel.ng, hi@sojiare.com, hi@gavel.ng <EditButton>Edit</EditButton>
                                </TableContent>
                            </div>
                        </TableContainer>

                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>Website</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>
                                    www.gavel.ng <EditButton>Edit</EditButton>
                                </TableContent>
                            </div>
                        </TableContainer>

                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>About</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>
                                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Tincidunt eget pharetra
                                    elementum in enim tellus. Convallis ut blandit sed elementum ipsum ut augue ornare.
                                    Diam aliquet sapien maecenas congue phasellus. A massa eu ultricies sollicitudin
                                    neque, dui morbi elit. Arcu pulvinar penatibus sit venenatis. Lorem ipsum dolor sit
                                    amet, consectetur adipiscing elit. Tincidunt eget pharetra elementum in enim tellus.
                                    Convallis ut blandit sed elementum ipsum ut augue ornare. Diam aliquet sapien
                                    maecenas congue phasellus. A massa eu ultricies sollicitudin neque, dui morbi elit.
                                    Arcu pulvinar penatibus sit venenatis. Lorem ipsum dolor sit amet, consectetur
                                    adipiscing elit. Tincidunt eget pharetra elementum in enim tellus. Convallis ut
                                    blandit sed elementum ipsum ut augue ornare. Diam aliquet sapien maecenas congue
                                    phasellus. A massa eu ultricies sollicitudin neque, dui morbi elit. Arcu pulvinar
                                    penatibus sit venenatis.
                                    <EditButton onClick={() => setEditAboutModal(true)}>Edit</EditButton>
                                </TableContent>
                            </div>
                        </TableContainer>
                    </Table>
                </Container>
            </div>

            <Modal visible={editAboutModal} onRequestClose={() => setEditAboutModal(false)}>
                <EditAbout onRequestClose={() => setEditAboutModal(false)} />
            </Modal>
            <Modal visible={editPhoneNumberModal} onRequestClose={() => setEditPhoneNumberModal(false)}>
                <EditPhoneNumber onRequestClose={() => setEditPhoneNumberModal(false)} />
            </Modal>
        </>
    );
}
