import React, { useState } from 'react';
import styled from 'styled-components';
import Button from '@components/libs/Button';
import { colors } from '@components/typo';
import {Input} from '@components/libs/Forms'

const Container = styled.div`
    .modalContainer {
        width: 500px;
        background-color: #fff;
        border-radius: 10px;
        padding: 15px;
    }
    @media only screen and (max-width: 500px) {
        .modalContainer {
            width: 90vw;
        }
    }
`;

const Header = styled.div`
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
`

const ActionButtonContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
`;

const SendButton = styled(Button)`
    width: 150px;
    height: 40px;
    background-color: ${colors.defaultColor};
    color: #fff;
    border-radius: 5px;
`;
const CancelButton = styled(Button)`
    width: 80px;
    height: 40px;
    background-color: ${colors.grey};
    border-radius: 5px;
    color: ${colors.textColor};
`;

const AddToFlag = ({ onRequestClose, pickupValue, presentLocation }) => {
   
    return (
        <Container>
            <div className="modalContainer">
                <Header>Add New Content</Header>
                <Input textarea />
                <ActionButtonContainer>
                    <CancelButton title="Cancel" onClick={() => onRequestClose()} />
                    <SendButton
                        className="button_container"
                        title="Add Content"
                        // loading={loading}
                        // onClick={() => addToVerify()}
                    />
                </ActionButtonContainer>
            </div>
        </Container>
    );
};

export default AddToFlag;
