import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Logo from '@components/assets/Logo/variate_1.png';
import { FaUserCircle } from 'react-icons/fa';
import { colors } from '@components/typo';
import SojiBaseInit from '@modules/SojiBase';
import useUserDetails from '@components/Hooks/useUserDetails';
import './index.css';

const Header = () => {
    const useUserDetail = useUserDetails();

    const [state, setState] = useState({
        onPreviousSearchesModal: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const logout = async () => {
        const SOJIBase = await SojiBaseInit;
        await SOJIBase.deleteCookie();
        useUserDetail.set(false);
    };

    return (
        <>
            <div className="main_header">
                <div className="main_header_wrapper container">
                    <div className="logo_container">
                        <img src={Logo} />
                    </div>
                    <div className="nav_bar_container">
                        <Link to="/company/dashboard/analysis">
                            <div className="nav_dot"></div> Analysis
                        </Link>
                        <Link to="/company/dashboard/documents">
                            <div className="nav_dot"></div>Document
                        </Link>
                        <Link to="/company/dashboard/reviews">
                            <div className="nav_dot"></div>Reviews
                        </Link>
                        <Link to="/company/dashboard/visitors">
                            <div className="nav_dot"></div>Visitors
                        </Link>
                    </div>

                    <label className="dropdown">
                        <div className="dd-button">
                            <div className="action_container">
                                <FaUserCircle size={30} color={colors.textColor} />
                                <div className="username">Gavel NG</div>
                            </div>
                        </div>
                        <input type="checkbox" className="dd-input" id="test" />
                        <ul className="dd-menu">
                            <li>Profile</li>
                            <li className="divider"></li>
                            <li>Upgrade Account</li>
                            <li className="divider"></li>
                            <li onClick={() => logout()}>Sign Out</li>
                        </ul>
                    </label>
                </div>
            </div>
        </>
    );
};

export default Header;
