import React, { lazy, useState, Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';
import styled from 'styled-components';

import Sidebar from '@components/layouts/Sidebar';
import Header from '@components/layouts/Header';

// Companies
import CompanyAnalysis from '../pages/Company/Dashboard/pages/Analysis';
import VerifiedContent from '../pages/Company/Dashboard/pages/VerifiedContent';
import CompanyReviews from '../pages/Company/Dashboard/pages/Reviews';
import CompanyVisitors from '../pages/Company/Dashboard/pages/Visitors';
import Profile from '../pages/Company/Dashboard/pages/Profile';

const Container = styled.div`
    height: 100vh;
    width: 100vw;
    display: flex;
`;

const Content = styled.div`
    flex: 1;
    overflow-y: auto;
    overflow-x: hidden;
    padding-top: 0px;
    height: 100vh;
    display: flex;
    flex-direction: column;
`;

const ContentWrapper = styled.div`
    display: flex;
    flex-direction: column;
    margin-top: 2px;
    padding: 15px;
    background: #fff;
`;

export default function Access() {
    const [collapsed, setCollapsed] = useState(false);
    const [toggled, setToggled] = useState(false);

    const handleCollapsedChange = (checked) => {
        console.log(checked);
        setCollapsed(checked);
    };

    const handleToggleSidebar = (value) => {
        setToggled(value);
    };
    return (
        <Container>
            <Sidebar
                collapsed={collapsed}
                toggled={toggled}
                handleToggleSidebar={handleToggleSidebar}
                handleCollapsedChange={handleCollapsedChange}
            />
            <Content>
                <Header />
                <div>
                    <ContentWrapper className="container">
                        <Suspense fallback={<></>}>
                            <Routes>
                                <Route path="/">
                                    <Route path="company/dashboard/analysis" element={<CompanyAnalysis />} />
                                    <Route path="company/dashboard/verified_content" element={<VerifiedContent />} />
                                    <Route path="company/dashboard/reviews" element={<CompanyReviews />} />
                                    <Route path="company/dashboard/visitors" element={<CompanyVisitors />} />
                                    <Route path="company/dashboard/profile" element={<Profile />} />
                                </Route>
                            </Routes>
                        </Suspense>
                    </ContentWrapper>
                </div>
            </Content>
        </Container>
    );
}
